import { Component, Input, Output, EventEmitter } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { Observable } from "rxjs/Rx";

@Component({
	selector: "auth-signin",
	template: `<div fxLayout='row' fxLayoutAlign="center center">
    <ngs-form-view 
        [id]="formId"
        [card]="true"
        (accept)="signin($event)"
    ></ngs-form-view>
</div>
<!-- <div>
    <a fxFlex="nogrow" routerLink="/user/password/reset" mat-raised-button fxFlexFill>Ú©ÙÙÙ Ø¹Ø¨ÙØ± Ø®ÙØ¯ Ø±Ø§ ÙØ±Ø§ÙÙØ´ Ú©Ø±Ø¯Ù Ø§ÛØ¯Ø</a>
</div> -->`,
	styles: [``]
})
export class SigninComponent {
	@Output() signedin = new EventEmitter();

	@Input() formId: string;

	signin(formGroup: FormGroup) {
		if (!formGroup.valid) return;
		this.signedin.emit(formGroup.value);
	}

	constructor() {}
}
